import React from 'react';
import {
    Slider,
    Switch
} from 'antd';
import EditingType from '@/constant/EditingType';
import AttributeItem from './AttributeItem';

export default class NodeStyle extends React.Component {
    onSizeChange = (value) => {
        this.props.updateNodeSize(value);
        this.props.updateEditingType(EditingType.UPDATE_STYLE);
    }

    onOpacityChange = (value) => {
        this.props.updateNodeOpacity(value);
        this.props.updateEditingType(EditingType.UPDATE_STYLE);
    }

    onLabelChange = (checked) => {
        this.props.updateNodeLabel(checked);
        this.props.updateEditingType(EditingType.UPDATE_STYLE);
    }

    render() {
        const { intl, initDone, nodeSize, nodeOpacity, nodeLabel } = this.props;
        return (
            <div>
                <AttributeItem name={initDone && intl.get("Size")}>
                    <Slider
                        max={20}
                        step={1}
                        value={nodeSize}
                        style={{ margin: '5px 0' }}
                        onChange={this.onSizeChange}
                    />
                </AttributeItem>
                <AttributeItem name={initDone && intl.get("Opacity")}>
                    <Slider
                        max={1}
                        step={0.01}
                        value={nodeOpacity}
                        style={{ margin: '5px 0' }}
                        onChange={this.onOpacityChange}
                    />
                </AttributeItem>
                <AttributeItem name={initDone && intl.get("Label")}>
                    <Switch
                        size="small"
                        checked={nodeLabel}
                        onChange={this.onLabelChange}
                    />
                </AttributeItem>
            </div>
        );
    }
}